.registerName {
  margin-bottom: -30px;
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  position: relative;
  align-items: center;
  height: 30px;
  margin-bottom: 20px;
  background-color: chocolate;
}
.registerName-label {
  position: absolute;
  border-bottom: 1px solid black;
  height: 100%;
  width: 100%;
  pointer-events: none;
  bottom: 0px;
}

.registerName-input {
  height: 100%;
  width: 100%;
  color: rgb(0, 0, 0);
  border: none;
  outline: none;
}
.registerName-span {
  position: absolute;
  bottom: 0px;
  left: 1px;
  transition: 0.3s ease;
}

.registerName-input:focus + .registerName-label .registerName-span,
.registerName-input:valid + .registerName-label .registerName-span {
  transform: translateY(-155%);
  font-size: 15px;
  color: #03058b;
}

