.loginUsername {
  margin-bottom: -30px;
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  position: relative;
  align-items: center;
  height: 30px;
  margin-bottom: 20px;
  background-color: chocolate;
}
.loginUsername-label {
  position: absolute;
  border-bottom: 1px solid black;
  height: 100%;
  width: 100%;
  pointer-events: none;
  bottom: 0px;
}

.loginUsername-input {
  height: 100%;
  width: 100%;
  color: rgb(0, 0, 0);
  border: none;
  outline: none;
}
.loginUsername-span {
  position: absolute;
  bottom: 0px;
  left: 1px;
  transition: 0.3s ease;
}

.loginUsername-input:focus + .loginUsername-label .loginUsername-span,
.loginUsername-input:valid + .loginUsername-label .loginUsername-span {
  transform: translateY(-150%);
  font-size: 15px;
  color: #03058b;
}

